import React, {Component} from 'react'

import './style.scss'

const rowLabels = [
  {key: 'ones', label: 'Ones'},
  {key: 'twos', label: 'Twos'},
  {key: 'threes', label: 'Threes'},
  {key: 'fours', label: 'Fours'},
  {key: 'fives', label: 'Fives'},
  {key: 'sixes', label: 'Sixes'},
  {key: 'totalTop', label: 'Total 1-6'},
  {key: '3x', label: '3 of Kind'},
  {key: '4x', label: '4 of Kind'},
  {key: 'fullHouse', label: 'Full House'},
  {key: 'smallStraight', label: 'S Straight'},
  {key: 'largeStraight', label: 'L Straight'},
  {key: 'yhatzee', label: 'YHATZEE'},
  {key: 'chance', label: 'Chance'},
  {key: 'bonusYhatzee', label: 'YHATZEE Bonus'},
  {key: 'bonusTop', label: 'Top Bonus'},
  {key: 'total', label: 'Score'},
]

class Scoreboard extends Component {
  state = {
    playerToAdd: '',
    players: [],
    playerScores: {},
  }

  render = () => {
    console.log('render!')
    const {dice} = this.props
    const {players, playerToAdd, playerScores} = this.state

    return (
      <div className="Scoreboard">
        <div className="Scoreboard-RowLabels">
          <div className="Scoreboard-RowLabels-Label" />
          {rowLabels.map((row, i) =>
            <div key={i} className="Scoreboard-RowLabels-Label">
              {row.label}
            </div>
          )}
        </div>
        {players.map(player =>
          <div key={player.name} className="Scoreboard-Scores-Column">
            <div className="Scoreboard-Scores-Column-Header">
              {player.name}
            </div>
            {rowLabels.map(row =>
              <div
                key={row.key}
                className="Scoreboard-Scores-Column-Score"
              >
                <input
                  className={row.key}
                  ref={`${player.name}-${row.key}`}
                  onBlur={() => {this.changeScore(player.name, row.key, `${player.name}-${row.key}`)}}
                />
              </div>
            )}
          </div>
        )}
        <div className="Scoreboard-AddPlayer">
          <form onSubmit={this.addPlayer}>
            <input
              placeholder="Add player"
              value={playerToAdd}
              onChange={this.changePlayerToAdd}
            />
            <button>Add</button>
          </form>
        </div>
      </div>
    )
  }

  changeScore = (player, row, ref) => {
    if (!this.refs[ref].value) {
      return
    }
    const players = [...this.state.players]
    const playerObj = players.find(o => o.name === player)
    const playerScores = Object.assign({}, this.state.playerScores)
    const currentPlayerScores = playerScores[player]
    currentPlayerScores[row] = this.refs[ref].value
    playerScores[player] = currentPlayerScores
    if (row === 'yhatzee' && parseInt(this.refs[`${player}-yhatzee`].value) === 50) {
      playerObj.numYhatzees++
    } else if (this.isYhatzee() && playerObj.numYhatzees > 0) {
      playerObj.numYhatzees++
    }

    this.setState({
      players,
      playerScores,
    }, () => {
      this.updateTotals(player)
    })
  }

  updateTotals = (player) => {
    const {players} = this.state
    const playerObj = players.find(o => o.name === player)
    const {numYhatzees} = playerObj
    const topTotal = (parseInt(this.refs[`${player}-ones`].value) || 0)
      + (parseInt(this.refs[`${player}-twos`].value) || 0)
      + (parseInt(this.refs[`${player}-threes`].value) || 0)
      + (parseInt(this.refs[`${player}-fours`].value) || 0)
      + (parseInt(this.refs[`${player}-fives`].value) || 0)
      + (parseInt(this.refs[`${player}-sixes`].value) || 0)
    if (topTotal >= 63) {
      this.refs[`${player}-bonusTop`].value = 35
    }
    this.refs[`${player}-totalTop`].value = topTotal
    if (this.isYhatzee() && playerObj.numYhatzees > 1) {
      this.refs[`${player}-bonusYhatzee`].value = (parseInt(this.refs[`${player}-bonusYhatzee`].value) || 0) + 50
    }
    const total = topTotal
      + (parseInt(this.refs[`${player}-3x`].value) || 0)
      + (parseInt(this.refs[`${player}-4x`].value) || 0)
      + (parseInt(this.refs[`${player}-fullHouse`].value) || 0)
      + (parseInt(this.refs[`${player}-smallStraight`].value) || 0)
      + (parseInt(this.refs[`${player}-largeStraight`].value) || 0)
      + (parseInt(this.refs[`${player}-yhatzee`].value) || 0)
      + (parseInt(this.refs[`${player}-chance`].value) || 0)
      + (parseInt(this.refs[`${player}-bonusTop`].value) || 0)
      + (parseInt(this.refs[`${player}-bonusYhatzee`].value) || 0)
    this.refs[`${player}-total`].value = total
}

  isYhatzee = () => {
    const {dice} = this.props
    const diceValues = [...new Set(dice.map(die => die.value))]
    return diceValues.length === 1
  }

  changePlayerToAdd = (event) => {
    const players = [...this.state.players]
    const player = {
      name: event.target.value,
      numYhatzees: 0,
    }
    this.setState({
      playerToAdd: event.target.value,
    })
  }

  addPlayer = (event) => {
    event.preventDefault()
    const {playerToAdd} = this.state
    const players = [...this.state.players]
    players.push({name: playerToAdd, numYhatzees: 0})
    const playerScores = Object.assign({}, this.state.playerScores)
    const scoreObject = {}
    rowLabels.forEach(rowLabel => scoreObject[rowLabel.key] = null)
    playerScores[playerToAdd] = scoreObject
    this.setState({
      players,
      playerScores,
      playerToAdd: '',
    })
  }

}

export default Scoreboard
