import React, {Component} from 'react'
import './style.scss'

class Die extends Component {
  render = () => {
    const {rolling, value, saved, onClick} = this.props
    return rolling && !saved
    ? <div className="Die Die-Rolling"></div>
    : <div
        className={`Die Die-${value} ${saved ? 'saved' : ''}`}
        onClick={onClick}
      >
        {value}
      </div>
  }
}

export default Die