import React, {Component} from 'react'
import Die from './../Die'
import Scoreboard from './../Scoreboard'
import _ from 'lodash'
import './style.scss'

const makeDice = () => {
  return [
    {saved: false, value: _.random(1, 6)},
    {saved: false, value: _.random(1, 6)},
    {saved: false, value: _.random(1, 6)},
    {saved: false, value: _.random(1, 6)},
    {saved: false, value: _.random(1, 6)},
  ]
}

class DiceSet extends Component {
  state = {
    rolling: true,
    rollIndex: 0,
    dice: makeDice(),
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        rolling: false,
      })
    }, 1000)
  }

  render = () => {
    const {rolling, rollIndex, dice} = this.state

    return (
      <div>
        <div className="DiceSet">
          <div className={`DiceSet-Roll ${rollIndex === 2 ? 'last' : ''}`}>Roll {rollIndex + 1}</div>
          <div className="DiceSet-Dice">
            {dice.map((die, i) => <Die
              key={i}
              value={die.value}
              saved={die.saved}
              rolling={rolling}
              onClick={() => {this.onDieClick(i)}}
            />)}
          </div>
          <div className="DiceSet-Buttons">
            <button onClick={this.rollDice} disabled={rolling || rollIndex > 1}>
              roll again!
            </button>
            <button onClick={this.onNextTurn}>
              next turn!
            </button>
          </div>
        </div>
        <Scoreboard dice={dice} />
      </div>
    )
  }

  onNextTurn = () => {
    this.setState({
      rolling: true,
      rollIndex: -1,
      dice: makeDice(),
    }, () => {
      this.rollDice()
    })
  }

  onDieClick = (i) => {
    const dice = [...this.state.dice]
    dice[i].saved = !dice[i].saved
    this.setState({dice})
  }

  rollDice = () => {
    const {rollIndex} = this.state
    const dice = [...this.state.dice].map((die) => {
      die.value = !die.saved ? _.random(1, 6) : die.value
      return die
    })
    this.setState({
      rolling: true,
      rollIndex: rollIndex + 1,
      dice,
    }, () => {
      setTimeout(() => {
        this.setState({
          rolling: false,
        })
      }, 500)
    })
  }
}

export default DiceSet
